<template>
  <div class="add-rule">
    <el-row>
      <el-col :span="6">
        <div class=" ">&nbsp;</div>
      </el-col>
      <el-col :span="12">
        <div class=" "><h2>基本信息</h2></div>
        <el-row class="form-item" v-if="ruleDetail.id">
          <el-col :span="4">ID</el-col>
          <el-col :span="16">{{ruleDetail.id}}</el-col>
        </el-row>
        <el-row class="form-item">
          <el-col :span="4">报价规则名称：</el-col>
          <el-col :span="16"><el-input v-model="ruleDetail.name" placeholder="请输入名称"></el-input></el-col>
        </el-row>
        <el-row class="form-item">
          <el-col :span="4">作用区域公司：</el-col>
          <el-col :span="16">
            <el-select v-model="ruleDetail.company" placeholder="请选择">
            <el-option
            v-for="item in companyOptions"
            :key="item.value"
            :label="item.text"
            :value="item.value">
            </el-option>
            </el-select>
          </el-col>
        </el-row>
        <el-row class="form-item">
          <el-col :span="4">客户类型：</el-col>
          <el-col :span="16">
            <el-select v-model="ruleDetail.type" placeholder="请选择">
              <el-option
                v-for="item in customerOptions"
                :key="item.value"
                :label="item.text"
                :value="item.value">
              </el-option>
            </el-select>
          </el-col>
        </el-row>
        <el-row class="form-item">
          <el-col :span="4">状态：</el-col>
          <el-col :span="16">
            <el-select v-model="ruleDetail.status" placeholder="请选择">
              <el-option
                v-for="item in stateOptions"
                :key="item.value"
                :label="item.text"
                :value="item.value">
              </el-option>
            </el-select>
          </el-col>
        </el-row>
        <div class=" "><h2>加价率</h2></div>
        <el-row class="form-item" v-for="(item,index) in ruleDetail.uprate" :key="item">
          <el-col :span="18">
            <el-input-number v-model="item.rangestart" @change="handleStart(item,index)" :min="1" :max="item.rangeend"></el-input-number> --
            <el-input-number v-model="item.rangeend" @change="handleEnd(item,index)" :min="item.rangestart" :max="200000"></el-input-number>
            元
          </el-col>
          <el-col :span="4">
            <el-row>
              <el-col :span="18"><el-input v-model="item.val" @change="changeVal(item)"></el-input></el-col>
              <el-col :span="6">%</el-col>
            </el-row>
          </el-col>
          <el-col :span="2">
            <span @click="removeRange(index)"><i class="el-icon-delete"></i></span>
          </el-col>
        </el-row>
        <el-row>
          <el-button class="large-btn" type="primary"  size="small" icon="plus" @click="addRange">新增区间</el-button>
        </el-row>
        <el-row>
          <el-button @click="cancelFn">取消</el-button>
          <el-button type="primary" @click="saveFn">保存</el-button>
        </el-row>
      </el-col>
      <el-col :span="6">
        <div class=" ">&nbsp;</div>
      </el-col>
    </el-row>

  </div>
</template>

<script>
  import {mapState,mapMutations} from 'vuex'
  import ElRow from "element-ui/packages/row/src/row";
  import ElCol from "element-ui/packages/col/src/col";
  import ElInput from "../../../node_modules/element-ui/packages/input/src/input";
  import ElButton from "../../../node_modules/element-ui/packages/button/src/button";
  export default {
    components: {
      ElButton,
      ElInput,
      ElCol,
      ElRow
    },
    data () {
      return {
        companyOptions:[],
        customerOptions:[
          {
            "value": "默认类型",
            "text": "默认类型"
          },
          {
            "value": "大客户",
            "text": "大客户"
          },
          {
            "value": "合作店",
            "text": "合作店"
          }
        ],
        stateOptions:[
          {
            "value": "禁用",
            "text": "禁用"
          },
          {
            "value": "可用",
            "text": "可用"
          }
        ]
      }
    },
    computed:{
      ...mapState(
          ['ruleDetail']
      )
    },
    methods:{
      ...mapMutations([
        "setSecondMenu","setFirstMenu","setRuleDetail"
      ]),
      cancelFn(){
          this.$router.push({
            "path":"/rule"
          })
      },
      saveFn(){
          console.log(this.ruleDetail)
      },
      handleStart(data,index){
        if(this.ruleDetail.uprate[index-1]){
          setTimeout(()=>{
            this.ruleDetail.uprate[index-1].rangeend=data.rangestart
            if(this.ruleDetail.uprate[index-1].rangestart>=data.rangestart){
              this.ruleDetail.uprate[index-1].rangestart=data.rangestart
            }
          },200)
        }
      },
      handleEnd(data,index){
        if(this.ruleDetail.uprate[index+1]){
          if(data){
            setTimeout(()=>{
              this.ruleDetail.uprate[index+1].rangestart=data.rangeend
              if(this.ruleDetail.uprate[index+1].rangeend<=data.rangeend){
                this.ruleDetail.uprate[index+1].rangeend=data.rangeend
              }
            },200)
          }
        }
      },
      removeRange(index){
//        this.$confirm('确认删除吗？').then((res)=>{
//            console.log(res)
//        }).catch((res)=>{
//            console.log(res)
//        })
        this.ruleDetail.uprate.splice(index,1)
        this.handleEnd(this.ruleDetail.uprate[index-1],index-1)
      },
      addRange(){
        var uprate=this.ruleDetail.uprate
        console.log(uprate)
        uprate.push({
          "rangestart":0,"rangeend":100,"val":"0"
        })
        this.handleEnd(uprate[uprate.length-2],uprate.length-2)
      },
      changeVal(item){
        if(isNaN(item.val)||item.val<0||item.val=="0"){
          item.val=1
        }
      }
    },
    mounted(){
      this.setFirstMenu("报价规则管理")
      if(!this.ruleDetail.id){
        this.setSecondMenu("新增报价规则")
        this.setRuleDetail({
          "id":"",
          "name":"",
          "company":"",
          "type":"默认类型",
          "uprate":[

          ],
          "status":"可用"
        })
      }else{
        this.setSecondMenu("修改报价规则")
      }
//      console.log(this.$route.query.id)
      if(!this.ruleDetail.id && this.$route.query.id!=0){
        this.$router.push({
          path: '/rule'
        })
      }
      this.$http.post("company").then((res)=>{
          this.companyOptions=JSON.parse(res.body)
      })
    }
  }
</script>

<style scoped>
  .el-icon-delete{
    cursor: pointer;
  }
  .el-icon-delete:hover{
    color: #e64242;
  }
  .large-btn{
    width: 100%;
    margin-bottom: 20px;
  }
  .form-item{
    margin-bottom:15px;
  }
  .form-item div{
    height: 40px;
    line-height: 40px;
  }
  .add-rule{
    margin-top: 10px;
  }
  h2{
    display: inline-block;
    font-weight: bold;
    font-size: 16px;
    border-left: 4px solid #20a0ff;
    padding: 5px 10px;
    margin: 10px 0;
  }
</style>



