<template>
  <div class="diff-qa">
    <div v-for="(item,index) in qaList" :key="item">
      <el-row class="clrfix">
        <el-row class="no-border">
          <el-col :span="1">
            <div class="grid-content "></div>
          </el-col>
          <el-col :span="3">
            <div class="grid-content bg-purple-dark">{{item.q.date}}</div>
          </el-col>
          <el-col :span="20">
            <div class="grid-content ">
              <p>{{item.q.name}}: {{item.q.content}}</p>
              <img v-for="(src,idx) in item.q.imgArr" :src="src" @click="showImg(item.a.imgArr,idx)" :key="src"  title="点击图片看大图">
            </div>
          </el-col>
        </el-row>
        <el-row class="no-border">
          <el-col :span="4">
            <div class="grid-content ">{{item.a.date}}</div>
          </el-col>
          <el-col :span="20">
            <div class="grid-content ">
              <p>{{item.a.name}}: {{item.a.content}}</p>
              <img v-for="(src,idx) in item.a.imgArr" :src="src" @click="showImg(item.a.imgArr,idx)" :key="src">
            </div>
          </el-col>
        </el-row>
      </el-row>
    </div>
  </div>
</template>

<script>
  import ElRow from "element-ui/packages/row/src/row";
  export default{
    components: {ElRow},
    props:['qaList'],
      data(){
          return {

          }
      },
      methods:{
        showImg(imgArr,idx){
            this.$emit('showImg',imgArr,idx)
        }
      }
  }
</script>

<style>
  .item .el-row .no-border{
    border-bottom: none;
    padding: 5px 0;
  }
 .diff-qa .el-col{
   /*margin-top: 5px;*/
 }
</style>



